








































































import { Component, Prop, Vue } from "vue-property-decorator";
import ITableData from "@/types/types";
import sideArrows from "@/utils/enums";
@Component({})
export default class AppWeatherDataTable extends Vue {
  @Prop({ required: true }) readonly isWeatherLoading!: boolean;
  @Prop({ required: true, default: [] }) readonly forecastsData!: Array<object>;
  @Prop({ required: true, default: "" }) readonly city!: string;
  @Prop() readonly background!: String;

  normalizeMinMaxTemp(min: number, max: number): string {
    // добавление знака плюс и минус в зависимости от температуры
    // меньше или больше 0 и т.д.
    if (min > 0 && max > 0) {
      return `+${min.toFixed(0)}...+${max.toFixed(0)}`;
    } else if (min < 0 && max > 0) {
      return `-${min.toFixed(0)}...+${max.toFixed(0)}`;
    } else {
      return `-${min.toFixed(0)}...-${max.toFixed(0)}`;
    }
  }

  normalizeDateFormat(timestamp: number): string {
    // перевод из UNIX в читабельный формат
    return new Date(timestamp * 1000).toLocaleTimeString(
      "ru-ru",
      this.getOptionsDate
    );
  }

  normalizeProbability(pop: number): string {
    // преобразовывает десятичный коэффициент вероятности исхода в проценты
    return (pop * 100).toFixed(0);
  }

  getFeelLikesTemperatureDuringTheDay(temp: number, index: number): string {
    const partsDay: { [index: string]: string } = {
      day: "Днём:",
      night: "Ночью:",
      morn: "Утром:",
      eve: "Вечером:",
    };
    return `${partsDay[index]} ${this.getCorrectTempSign(temp)}`;
  }

  getSideArrows(deg: number): string {
    return `${deg}${sideArrows[(deg / 45) | 0]}`;
  }

  getCorrectTempSign(temp: number): string {
    if (temp > 0) {
      return `+${temp.toFixed(0)}`;
    } else if (temp < 0) {
      return `${temp.toFixed(0)}`;
    } else {
      return temp.toFixed(0);
    }
  }

  getTemperatureDuringFirstHalf(items: Array<object>, temp: any) {
    return [
      {
        name: "Утром:",
        temperature: this.getCorrectTempSign(temp.morn),
      },
      {
        name: "Днем:",
        temperature: this.getCorrectTempSign(temp.day),
      },
    ];
  }
  getTemperatureDuringSecondHalf(items: Array<object>, temp: any) {
    return [
      {
        name: "Вечером:",
        temperature: this.getCorrectTempSign(temp.eve),
      },
      {
        name: "Ночью:",
        temperature: this.getCorrectTempSign(temp.night),
      },
    ];
  }

  get getOptionsDate(): object {
    return {
      weekday: "short",
      month: "short",
      day: "2-digit",
    };
  }
  get getTableHeaders(): Array<ITableData> {
    return [
      {
        text: "Дата",
        align: "start",
        sortable: false,
        value: "dt",
      },
      { text: "Погода", value: "weather", sortable: false, align: "center" },
      {
        text: "Температура",
        value: "tempMinMax",
        sortable: false,
        align: "center",
      },
      {
        text: "Скорость ветра, м/c",
        value: "wind_speed",
        sortable: false,
        align: "center",
      },
      {
        text: "Направление ветра, м/c",
        value: "wind_deg",
        sortable: false,
        align: "center",
      },
      {
        text: "Давление, мм рт. ст.",
        value: "pressure",
        sortable: false,
        align: "center",
      },
      {
        text: "Влажность, %",
        value: "humidity",
        sortable: false,
        align: "center",
      },
      {
        text: "Облачность, %",
        value: "clouds",
        sortable: false,
        align: "center",
      },
      {
        text: "Вероятность выпадения осадков, %",
        value: "pop",
        align: "center",
        sortable: false,
      },
      {
        text: "Объем осадков, мм",
        value: "rain",
        sortable: false,

        align: "center",
      },
    ];
  }
}
